html {
  background: radial-gradient(at top left, blue, red);
  background-size: cover;
  background-position: fixed;
  height: 110vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sub {
  display: flex;
  width: 100vw;
}

.sub-side {
  display: flex;
  min-width: 15vw;
}

.sub-main {
  display: flex;
  padding: 1em;
  min-width: 75vw;
  min-height: 50vh;
  margin-left: .5em;
  background: rgb(240, 248, 255, .5);
  border: 1px solid black;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: rgb(240, 248, 255, .5);
  border: 1px solid black;
  margin-bottom: 1em;
}

.preview {
  
  
}

.side-nav {
  background: rgb(240, 248, 255, .5);
  width: 20vw;
  height: 100%;
  border: 1px solid black;
  margin-right: .5em;
  padding-top: 3em;
}

.icon {
  height: 1.5em;
  width: 1.5em;
}

.nav-icon {
  background: url("./menu-icon.svg");
  background-size: cover;
}

.nav-item {
  padding: 1em;
  border-bottom: 1px solid rgb(255, 0, 0, .3);

}

.nav-item:hover {
  background: rgb(255, 0, 0, .3);
}
